nav {
  background-color: rgb(198, 198, 198);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.8rem;
  /* margin: 0.6rem; */
}

#navigationList {
  display: flex;
  flex-direction: row;
}

#navigationList li {
  list-style-type: none;
}

#navigationList li a {
  text-decoration: none;
  color: rgb(56, 56, 56);
  padding: 0.3rem;
  font-weight: 600;
}

#navigationList li a:hover {
  color: rgb(128, 128, 128);
}

#navigationList li a:hover::after,
#navigationList li a.active::after {
  content: "";
  width: 30%;
  height: 2px;
}

button {
  border: none;
  background: transparent;
}
button:hover {
  background: none;
}
#mobile {
  display: none;
}

@media screen and (max-width: 769px) {
  #mobile {
    display: block;
  }

  #navigationList {
    position: fixed;
    top: 5.8rem;
    right: 0;
    padding: 0.8rem;
    border-radius: 10px 0 0 10px;
    background: rgb(107, 145, 217);
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    transition: 0.3s ease-in-out;
  }

  #navigationList.hidden {
    right: -300px;
  }

  #navigationList li a {
    color: white;
  }
  #navigationList li a:hover {
    color: rgb(37, 55, 88);
  }
  #navigationList li {
    margin: 10px;
  }
}
